<template>
  <div class="container" v-if="userId != ''">
    <h3>ลงทะเบียน</h3>
    <h4>{{ userId }}</h4>
    <form>
  <div class="form-group">
    <label for="exampleInputEmail1">เบอร์โทร</label>
    <input type="tel" class="form-control" aria-describedby="emailHelp" placeholder="เบอร์โทร...">
  </div>
  <div class="text-center">
  <button type="submit" class="btn btn-success">ลงทะเบียน</button>
  </div>
</form>
  </div>
</template>
<script>
export default {
  name: "Register",
  data() {
    return {
      title: "ลงทะเบียน",
      userId: "",
      profile: [],
      send: false,
      tel: ""
    };
  },
  mounted() {
    console.log("mounted");
  },
  created() {
    console.log("created");
    this.loadLiff() 
  },
  methods: {
    loadLiff() {
      let vm = this;
      window.onload = function(e) {
        console.log(e);
        liff.init(function(data) {
          vm.initializeApp(data);
        });
      };
    },
    initializeApp: function(data) {
      let vm = this;
      //vm.name.push(data);
      vm.userId = data.context.userId;
      console.log(data);
    }
  }
};
</script>




